@import 'mixins';

.header{
    position: fixed;
    width: 100%;
    background: $white;
    border-bottom: 1px solid $orange;
    padding: 15px 0;
    z-index: 10;

    & .logo{
        height:40px;
        text-align: center;
    }

    & .lang-switcher{
        color: $orange;
        float: right;
        font-size: 18px;
        border: none;
        margin-left:20px;
        padding: 3px;
    }

    & .main-menu{
        list-style: none;
        margin: 0;
        padding: 0;
        height: 30px;
        & li{
            display: inline-block;
            line-height: 20px;
            vertical-align: middle;
            height: 20px;
            & a{
                color: $orange;
                text-transform: uppercase;
                font-size: 18px;
                display: inline-block;
                line-height: 20px;
                vertical-align: middle;
            }
        }
        &.text-left{
            & li{
                margin-right: 30px;
                &.shop-dropdown{
                    & a{
                        &:after{
                            content: "\f078";
                            font-family: $far;
                            font-weight: $regular;
                            margin-left:5px;
                            font-size: 10px;
                        }
                    }
                }
            }
        }
        &.text-right{
            & li{
                display: inline-block;
                margin-left: 30px;
                &.icon{
                    & a{ 
                        height: 20px;
                        width:20px;
                        text-indent: -9999px;
                        white-space: nowrap; 
                        overflow: hidden;  
                        font-size: 0; 
                    }
                    &.cart{
                        & a{
                            background: url(../img/bag-inactive.png);
                            background-repeat: no-repeat;
                            background-position: center center;
                        }
                    }
                    &.my-account{
                        & a{
                            background: url(../img/account-inactive.png);
                            background-repeat: no-repeat;
                            background-position: center center;
                        }
                    }
                    &.wishlist{
                        & a{
                            background: url(../img/wishlist-inactive.png);
                            background-repeat: no-repeat;
                            background-position: center center;
                        }
                    }
                }
            }
        }
    }
}

.shop-menu{
    position: absolute;
    width:100%;
    padding: 40px 0;
    top:70px;
    z-index: 10;
    left: 0;
    background: $blue;
    display: none;
    & .cat-menu{
        margin: 0;
        list-style:none;
        padding: 0;
        & li{
            border-bottom: 1px solid $orange;
            padding: 8px 15px;
            max-width: 200pxs;
            & a{ 
                color:$white;
                font-size: 16px;
            }
        }
    }
    & .image-menu{
        max-height: 300px;
        margin: 0 20px;
    }
}